import React, { ChangeEvent } from "react";
import TextField, { HelperText, Input } from "@material/react-text-field";
import List, { ListItem, ListItemText } from "@material/react-list";
import MaterialIcon from "@material/react-material-icon";
import { debounce } from "lodash";
import { connect } from "react-redux";
import { getCookie } from "../utils";
import { addCourse } from "../redux/actions";
import { SchedulesState } from "../redux/types";

interface AddCourseContentProps {
  currScheduleId: number;
  addCourse?: any;
  closeDialog?: any;
}

class AddCourseContent extends React.Component<AddCourseContentProps> {
  state = { value: "", results: [] };

  constructor(props: AddCourseContentProps) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.dialogClosing = this.dialogClosing.bind(this);
    this.search = debounce(this.search, 300).bind(this);
    this.resultSelected = this.resultSelected.bind(this);
  }

  resultSelected(selectedIndex: number) {
    const { dept, course_num } = this.state.results[selectedIndex];
    const body = {
      course_id: `${dept}-${course_num}`
    };
    const csrftoken = getCookie("csrftoken") as string;
    const headers = {
      "Content-Type": "application/json",
      "X-CSRFToken": csrftoken
    };
    const that = this;
    fetch(`/api/schedules/${this.props.currScheduleId}/courses`, {
      method: "PATCH",
      mode: "same-origin",
      headers,
      body: JSON.stringify(body)
    })
      .then((response: Response) => response.json())
      .then((json: JSON) => that.props.addCourse(json));
    this.dialogClosing();
  }

  search() {
    const that = this;
    fetch(`/api/data/search?q=${this.state.value}`)
      .then(response => response.json())
      .then(json => that.setState({ results: json }));
  }

  handleChange(e: any) {
    this.setState({ value: e.currentTarget.value });
    this.search();
  }

  dialogClosing() {
    this.setState({ results: [], value: "" });
    this.props.closeDialog();
  }
  render() {
    return (
      <div>
        <TextField
          helperText={<HelperText>Search</HelperText>}
          trailingIcon={<MaterialIcon role="button" icon="search" />}
        >
          <Input value={this.state.value} onChange={this.handleChange} />
        </TextField>
        <List
          className="course-results-list"
          handleSelect={this.resultSelected}
        >
          {this.state.results.map(({ name, dept, course_num }, i) => (
            <ListItem key={i}>
              <ListItemText primaryText={`${dept}-${course_num}: ${name}`} />
            </ListItem>
          ))}
        </List>
      </div>
    );
  }
}

const mapStateToProps = (state: SchedulesState) => {
  if (state.currScheduleName) {
    const schedule = state.schedules.get(state.currScheduleName);
    if (schedule) {
      return {
        currScheduleId: schedule.id
      };
    }
  }
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    addCourse: (json: any) => dispatch(addCourse(json))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AddCourseContent);
