import React, { Component } from "react";
import Drawer, {
  DrawerAppContent,
  DrawerHeader,
  DrawerContent
} from "@material/react-drawer";
import AddDialog from "./components/AddDialog";
import CourseList from "./components/CourseList";
import Fab from "@material/react-fab";

import MaterialIcon from "@material/react-material-icon";
import ScheduleSelect from "./components/ScheduleSelect";
import { SchedulesState, Schedule } from "./redux/types";
import { connect } from "react-redux";
import { loadSchedules } from "./redux/actions";
import Week from "./components/Week";

interface AppProps {
  anySchedulesCreated: boolean;
  schedules: Map<string, Schedule>;
  currScheduleName: string;
  loadSchedules: any;
}

class App extends Component<AppProps> {
  state = { dialogOpen: false };

  constructor(props: any) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  componentDidMount() {
    fetch("/api/schedules/")
      .then(response => response.json())
      .then(json => {
        this.props.loadSchedules(json);
      })
      .catch(err => console.log(err));
  }

  handleClick() {
    this.setState({ dialogOpen: !this.state.dialogOpen });
  }

  handleLogout() {
    window.location.href = "/auth/logout";
  }

  render() {
    const { schedules } = this.props;

    let scheduleHeader = null;
    if (this.props.anySchedulesCreated) {
      scheduleHeader = (
        <ScheduleSelect scheduleNames={Array.from(schedules.keys())} />
      );
    }
    return (
      <div className="drawer-container">
        <Drawer className="drawer">
          <DrawerHeader>{scheduleHeader}</DrawerHeader>

          <DrawerContent className="drawer-content">
            <CourseList />
          </DrawerContent>
        </Drawer>
        <DrawerAppContent className="drawer-app-content">
          <div className="week">
            <Week />
          </div>
          <Fab
            className="fab"
            icon={<MaterialIcon icon="add" />}
            onClick={this.handleClick}
          />
          <Fab
            mini
            className="logout"
            icon={<MaterialIcon icon="exit_to_app" />}
            onClick={this.handleLogout}
          />
        </DrawerAppContent>
        <AddDialog
          open={this.state.dialogOpen}
          closeDialog={this.handleClick}
        />
      </div>
    );
  }
}

const mapStateToProps = (state: SchedulesState) => ({
  anySchedulesCreated: Array.from(state.schedules.keys()).length !== 0,
  schedules: state.schedules,
  currScheduleName: state.currScheduleName
});

const mapDispatchToProps = (dispatch: any) => {
  return {
    loadSchedules: (schedules: Array<any>) => dispatch(loadSchedules(schedules))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
