const termCodeToString = termCode => {
  const termCodeStr = termCode.toString();
  const year = termCodeStr.substr(0, 4);

  let semester = termCodeStr[4];
  if (semester === "1") {
    semester = "Spring";
  } else if (semester === "2") {
    semester = "Summer";
  } else if (semester === "3") {
    semester = "Fall";
  } else if (semester === "4") {
    semester = "Full Year Professional";
  }

  let campus = termCodeStr[5];
  if (campus === "1") {
    campus = "College Station";
  } else if (campus === "2") {
    campus = "Galveston";
  } else if (campus === "3") {
    campus = "Qatar";
  } else if (campus === "5") {
    campus = "Half Year Term";
  }

  return semester + " " + year + " - " + campus;
};

class AddScheduleContent extends React.Component {
  state = { value: "", terms: [], activeTerm: null };
  constructor(props) {
    super(props);
    this.handleValueChange = this.handleValueChange.bind(this);
    this.submitScheduleName = this.submitScheduleName.bind(this);
    this.handleTermChange = this.handleTermChange.bind(this);
  }

  componentDidMount() {
    const that = this;
    fetch("/api/data/terms")
      .then(response => response.json())
      .then(json => {
        let active = null;
        if (json.length > 0) {
          active = json[0];
        }
        console.log(json);
        that.setState({
          terms: json.sort((a, b) => a < b),
          activeTerm: active
        });
      });
  }

  handleValueChange(e) {
    this.setState({ value: e.currentTarget.value });
  }

  submitScheduleName() {
    console.log(this.state.value);
    console.log(this.state.activeTerm);
    if (this.state.activeTerm === undefined || this.state.activeTerm === null) {
      return;
    }
    const body = {
      name: this.state.value,
      term_code: this.state.activeTerm
    };
    console.log(body);
    const csrftoken = getCookie("csrftoken");
    const headers = {
      "Content-Type": "application/json",
      "X-CSRFToken": csrftoken
    };
    const that = this;
    fetch(`/api/schedules/`, {
      method: "POST",
      headers,
      body: JSON.stringify(body)
    })
      .then(response => response.json())
      .then(json => {
        that.props.addSchedule(json);
      });
    this.setState({ value: "" });

    this.props.closeDialog();
  }

  handleTermChange(e) {
    this.setState({ activeTerm: e.target.value });
  }

  render() {
    const options = this.state.terms.map((term, i) => (
      <option key={i} value={term}>
        {termCodeToString(term)}
      </option>
    ));
    return (
      <div style={{ width: "100%" }}>
        <TextField
          label="Schedule Name"
          style={{ width: "100%" }}
          trailingIcon={<MaterialIcon role="button" icon="arrow_forward" />}
          onTrailingIconSelect={this.submitScheduleName}
        >
          <Input value={this.state.value} onChange={this.handleValueChange} />
        </TextField>
        <div style={{ width: "100%" }}>
          <Select onChange={this.handleTermChange}>{options}</Select>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    addSchedule: json => dispatch(addSchedule(json)),
    selectSchedule: name => dispatch(selectSchedule(name))
  };
};

export default connect(
  null,
  mapDispatchToProps
)(AddScheduleContent);
