import React from "react";
import List, {
  ListItem,
  ListItemText,
  ListItemMeta
} from "@material/react-list";
import IconButton from "@material/react-icon-button";
import MaterialIcon from "@material/react-material-icon";

import Collapsible from "react-collapsible";
import ConnectedSectionListItem, { SectionListItemProps } from "./SectionListItem";
import { removeCourse } from "../redux/actions";
import { connect } from "react-redux";
import { SchedulesState } from "../redux/types";
import { getCookie } from "../utils";

interface CourseListItemProps {
  dept: string;
  course_num: string;
  name: string;
  sections: Array<SectionListItemProps>;
  removeCourse: any;
  currScheduleId?: number;
}

class CourseListItem extends React.Component<
  CourseListItemProps
  > {
  state = { checked: false, indeterminate: false };

  constructor(props: CourseListItemProps) {
    super(props);
    this.handleDeleteClick = this.handleDeleteClick.bind(this);
  }

  handleDeleteClick() {
    this.props.removeCourse(this.props.dept, this.props.course_num);
    const course_id = `${this.props.dept}-${this.props.course_num}`
    const csrftoken = getCookie("csrftoken") as string;
    const headers = {
      'Content-Type': 'application/json',
      "X-CSRFToken": csrftoken
    }
    fetch(`/api/schedules/${this.props.currScheduleId}/courses/${course_id}`, { method: "DELETE", headers });
  }
  render() {
    const triggerElem = (
      <ListItem className="course-list-item">
        <ListItemText
          primaryText={`${this.props.dept}-${this.props.course_num}`}
          secondaryText={this.props.name}
        />
        <ListItemMeta
          meta={
            <IconButton onClick={this.handleDeleteClick}>
              <MaterialIcon icon="delete" />
            </IconButton>
          }
        />
      </ListItem>
    );

    return (
      <Collapsible trigger={triggerElem} transitionTime={250}>
        <List twoLine className="course-sections">
          {this.props.sections.map(({ section }, i) => {
            return <ConnectedSectionListItem key={i} section={section} />;
          })}
        </List>
      </Collapsible>
    );
  }
}

const mapStateToProps = (state: SchedulesState) => {
  if (state.currScheduleName) {
    const schedule = state.schedules.get(state.currScheduleName);
    if (schedule) {
      return {
        currScheduleId: schedule.id
      }
    }
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    removeCourse: (dept: string, course_num: string) => dispatch(removeCourse(dept, course_num))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CourseListItem);