import React, { ComponentProps } from "react";

import Dialog, { DialogTitle, DialogContent } from "@material/react-dialog";
import TabBar from "@material/react-tab-bar";
import Tab from "@material/react-tab";
import AddCourseContent from "./AddCourseContent";
import AddScheduleContent from "./AddScheduleContent";

interface AddDialogProps {
  open: boolean;
  closeDialog: any;
}

export default class AddDialog extends React.Component<AddDialogProps> {
  state = { activeIndex: 0 };

  handleActiveIndexUpdate = (activeIndex: number) => {
    this.setState({ activeIndex });
  };
  render() {
    const contents = [
      <AddCourseContent closeDialog={this.props.closeDialog} />,
      <AddScheduleContent closeDialog={this.props.closeDialog} />
    ];
    return (
      <Dialog open={this.props.open}>
        <DialogContent>
          <div>
            <TabBar
              activeIndex={this.state.activeIndex}
              handleActiveIndexUpdate={this.handleActiveIndexUpdate}
            >
              <Tab>
                <span className="mdc-tab__text-label">Course</span>
              </Tab>
              <Tab>
                <span className="mdc-tab__text-label">Schedule</span>
              </Tab>
            </TabBar>
            {contents[this.state.activeIndex]}
          </div>
        </DialogContent>
      </Dialog>
    );
  }
}
