import {
  SectionActionTypes,
  ADD_SECTION,
  REMOVE_SECTION,
  Course,
  CourseActionTypes,
  ADD_COURSE,
  REMOVE_COURSE,
  ScheduleActionTypes,
  ScheduleResponse,
  ADD_SCHEDULE,
  REMOVE_SCHEDULE,
  SELECT_SCHEDULE,
  LOAD_SCHEDULES
} from "./types";

export function addSection(sectionCrn: number): SectionActionTypes {
  return { type: ADD_SECTION, payload: sectionCrn };
}

export function removeSection(sectionCrn: number): SectionActionTypes {
  return { type: REMOVE_SECTION, payload: sectionCrn };
}

export function addCourse(course: Course): CourseActionTypes {
  return { type: ADD_COURSE, payload: course };
}

export function removeCourse(
  dept: string,
  course_num: string
): CourseActionTypes {
  return { type: REMOVE_COURSE, payload: { dept, course_num } };
}

export function addSchedule(response: ScheduleResponse): ScheduleActionTypes {
  return {
    type: ADD_SCHEDULE,
    payload: {
      name: response.name,
      id: response.id,
      term_code: response.term_code,
      selectedCrns: new Set<number>(),
      courses: {}
    }
  };
}

export function removeSchedule(name: string): ScheduleActionTypes {
  return { type: REMOVE_SCHEDULE, payload: name };
}

export function selectSchedule(name: string): ScheduleActionTypes {
  return { type: SELECT_SCHEDULE, payload: name };
}

export function loadSchedules(schedules: Array<any>): ScheduleActionTypes {
  return { type: LOAD_SCHEDULES, payload: schedules };
}
